import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../components/Login'
import Register from "../components/Register";
import MainPage from "../components/MainPage"
import AboutVirus from "../components/AboutVirus";
import Volunteer from "../components/Volunteer";
import Donate from "../components/Donate";
import MessageBoard from "../components/MessageBoard";
import MyPage from "../components/MyPage";
import RevisePwd from "../components/RevisePwd";
import PutMessage from "../components/PutMessage";

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    redirect: '/Login',
  },
  {
    path: '/Login',
    name: 'Login',
    component: Login,
  },
  {
    path: '/Register',
    name: 'Register',
    component: Register,
  },
  {
    path: '/MainPage',
    name: 'MainPage',
    component: MainPage,
    children: [
      {
        path: '/AboutVirus',
        name: 'AboutVirus',
        component: AboutVirus,
      },
      {
        path: '/Volunteer',
        name: 'Volunteer',
        component: Volunteer,
      },
      {
        path: '/Donate',
        name: 'Donate',
        component: Donate,
      },
      {
        path: '/MessageBoard',
        name: 'MessageBoard',
        component: MessageBoard,
      },
      {
        path: '/MyPage',
        name: 'MyPage',
        component: MyPage,
      },
      {
        path: '/RevisePwd',
        name: 'RevisePwd',
        component: RevisePwd,
      },
      {
        path: '/PutMessage',
        name: 'putMessage',
        component: PutMessage,
      },
    ]
  },
];

const router = new VueRouter({
  routes
});

router.beforeEach((to, from, next) => {
  if (to.path === '/Login') {
    next();
  } else if (to.path === '/Register') {
    next();
  } else {
    let token = localStorage.getItem('Authorization');
    if (token === null || token === '') {
      next('/Login');
    } else {
      next();
    }
  }
});

export default router
