var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('el-container',[_c('el-header',{attrs:{"id":"header"}},[_vm._v("关于新冠病毒的这十个问题，你一定要知道")]),_c('el-main',[_c('el-card',{staticClass:"box-card"},[_c('div',{staticClass:"clearfix",attrs:{"slot":"header"},slot:"header"},[_c('span',{staticClass:"title"},[_vm._v("一. 病毒的来源是什么？")])]),_c('div',[_c('p',[_vm._v(" 中国医学科学院等科研单位在《中华医学杂志》（英文版）发表的文章显示，新型冠状病毒与已知的冠状病毒相比相似度均小于90%，判定其为一种前所未知的新病毒。而新发现毒株包含ORF3和完整的ORF8基因区域，这是蝙蝠源冠状病毒的特征序列。 至于蝙蝠和人之间的中间宿主是什么，目前尚未有答案。不过，多项研究表明，华南海鲜市场或许并非新冠病毒的唯一发源地。 1月31日，生物预印本网站bioRxiv发表了印度理工学院德里分校研究人员的论文，声称新冠病毒插入了HIV基因结构片段，这在自然界中是不可能的，暗示是人造病毒。这种说法遭到了全球科学家一致的批评，同日《科学》发表文章称这种说法是“阴谋论”。bioRxiv很快进行了撤稿。目前科学界的意见普遍认为，这是一种来自自然界的病毒。")])])]),_c('el-card',{staticClass:"box-card"},[_c('div',{staticClass:"clearfix",attrs:{"slot":"header"},slot:"header"},[_c('span',{staticClass:"title"},[_vm._v("二. 通过什么传播？")])]),_c('div',[_c('p',[_vm._v(" 《新英格兰医学杂志》上的《中国出现新冠病毒——评估其影响时的关键问题》一文指出：新冠病毒的传播可能通过大的带毒飞沫和接触发生，较少通过气溶胶和污染物发生。这意味着，感染者的唾液会携带病毒，而说话、咳嗽、喷嚏，都会使唾液以飞沫方式传播到空气中，如此就有机会接触其他人的黏膜。同时，这些飞沫会沾在物体表面，比如电梯按钮，如果其他人的手触碰到再碰自己的眼睛，嘴巴等位置，也可能会被感染。 ")])])]),_c('el-card',{staticClass:"box-card"},[_c('div',{staticClass:"clearfix",attrs:{"slot":"header"},slot:"header"},[_c('span',{staticClass:"title"},[_vm._v("三. 传染性有多强？")])]),_c('div',[_c('p',[_vm._v(" 1月25日，由英国帝国理工学院发表的报告《新冠病毒的传播性》显示，如果没有有效的遏制措施，每个患者可能感染1.5至3.5个人。 1月30日《新英格兰医学期刊》刊登的《新冠病毒感染的肺炎在中国武汉的早期传播动力学》，测算认为，现有患者平均将感染传给了另外2.2人。 1月31日《柳叶刀》发表了一项来自香港大学研究团队的成果，认为在基线情况下，新冠病毒的RO（基本传染数）为2.68，即一个患者或病毒携带者平均可以传染给2.68个人。 总的来说，目前的研究认为新冠病毒基本传染数都在1.5-3之间，小于非典SARS病毒（香港）的3.1-4.2，那为何中国病例增长却超过2003年SARS的新增速度？这可能是由于新冠病毒的传播周期更快，传播方式更为隐匿，更难以防控，另外一个很重要的原因是，中国的人口流动速度远远超过2003年。 ")])])]),_c('el-card',{staticClass:"box-card"},[_c('div',{staticClass:"clearfix",attrs:{"slot":"header"},slot:"header"},[_c('span',{staticClass:"title"},[_vm._v("四. 发病的潜伏期是多少天？")])]),_c('div',[_c('p',[_vm._v(" 1月30日《新英格兰医学期刊》刊登的《新冠病毒感染的肺炎在中国武汉的早期传播动力学》表明，大多数感染者7天内会发病，估计平均潜伏期为5.2天。荷兰的研究人员表示，在34个确证病例中，平均潜伏期大概为5.8天。而在《新英格兰医学期刊》的另一则报道中，一位27岁的越南小伙被其曾到过武汉的父亲感染，发病的潜伏期只有3天。 世卫组织在《Novel Coronavirus(新冠病毒)-Situation Report-7》中预测病毒的潜伏期大约为2至10天。美国疾控中心的官员估计，潜伏期为2至14天。 目前有研究发现，可能有“无症状感染者”的存在，但对“无症状传播”尚存在争论，也不清楚疾病的严重程度是否会影响传播病毒的难易程度。2月1日，世卫组织在推特上声称：“无症状者的传染比较少见，对于其他冠状病毒来说也非常罕见，比如MERS。因此，无症状病例的传播可能不是此次冠状病毒传播的主要途径。” ")])])]),_c('el-card',{staticClass:"box-card"},[_c('div',{staticClass:"clearfix",attrs:{"slot":"header"},slot:"header"},[_c('span',{staticClass:"title"},[_vm._v("五. 发病的症状是什么？")])]),_c('div',[_c('p',[_vm._v(" 国家卫健委公布的《新冠病毒肺炎诊疗方案》(第四版)显示，以发热、乏力、干咳为主要表现。少数患者伴有鼻塞、流涕、腹泻等症状。重型病例多在一周后出现呼吸困难，严重者快速进展为急性呼吸窘迫综合征、脓毒症休克、难以纠正的代谢性酸中毒和出凝血功能障碍。重型、危重型患者病程中可为中低热，甚至无明显发热。 1月24日在《柳叶刀》上发表的论文《武汉市2019年新冠病毒感染患者的临床特征》发现，发病时的常见症状为发烧（98％），咳嗽（76％）和乏力或肌肉酸痛（44％），从发病到呼吸困难的天数平均为8天。 而美国首例患者最初表现为轻度咳嗽和低度间歇性发烧，入院前有两天的恶心和呕吐史。入院后出现大便稀疏的症状，还出现反复发烧的情况。在患病的第4天X光片还没有肺炎迹象，但患病第9天（住院的第五天）的X光片显示，左肺叶下有肺炎，血氧饱和度下降至90%，患者出现呼吸困难。这表明，该病的早期症状很像感冒，但在进程中会发生爆发性肺炎。 ")])])]),_c('el-card',{staticClass:"box-card"},[_c('div',{staticClass:"clearfix",attrs:{"slot":"header"},slot:"header"},[_c('span',{staticClass:"title"},[_vm._v("六. 危重率和死亡率高吗？")])]),_c('div',[_c('p',[_vm._v(" 2月4日，国家卫健委医政医管局副局长焦雅辉表示，截至2月3日24时，全国确诊病例病死率2.1%。湖北省确诊病例病死率3.1%，武汉确诊病例病死率4.9%，如果除掉湖北，其他省份病死率是0.16%。世卫组织突发卫生事件执行主任迈克尔·瑞安认为，与季节性流感或其他疾病的病死率相比，2%的病死率仍然是一个棘手的病死率。 1月29日发表于国际医学期刊柳叶刀的一篇回顾性研究文章表明，新冠病毒感染更可能影响患有合并症的老年男性，另外，年龄大、肥胖和合并症可能与死亡率增加有关。 虽然关于死亡率目前还无法下定论，但学界普遍认为，该病毒的死亡率大大低于另外两种冠状病毒：致死率三分之一的MERS，以及致死率约十分之一的SARS。从临床来看，MERS和SARS似乎对肺组织更具破坏性。 ")])])]),_c('el-card',{staticClass:"box-card"},[_c('div',{staticClass:"clearfix",attrs:{"slot":"header"},slot:"header"},[_c('span',{staticClass:"title"},[_vm._v("七. 目前有什么治疗方式？")])]),_c('div',[_c('p',[_vm._v(" 按照目前国家卫健委公布的诊疗方案，除了常规的病情检查和监测外，可对患者进行抗病毒治疗，发病早期可试用α-干扰素雾化吸入，也可选用洛匹那韦/利托那韦。如有肺部状况，酌情酌量短期内使用糖皮质激素；避免盲目或不恰当使用抗菌药物，尤其是联合使用广谱抗菌药物。加强细菌学监测，有继发细菌感染证据时及时应用抗菌药物。 这其中，洛匹那韦/利托那韦是一种抗艾滋病毒的复方制剂。北大第一医院呼吸内科主任王广发在感染新冠病毒后，就是使用了洛匹那韦/利托那韦。 而美国首个病例在肺部发炎之前的治疗在很大程度上也是支持性的。肺部发炎后开始使用万古霉素和头孢吡肟治疗，并开始吸氧。入院第七天，主治医师给病人静脉注射了瑞德西韦（Remdesivir），患者的呼吸得到了改善，血氧也升高了。 瑞德西韦是一种正在开发的新型核苷酸类似物前药，本来用于抗埃博拉病毒，在之前的临床试验中证实了对SARS和MERS的病毒病原体均有活性。 湖北省科技厅厅长王炜2月2日表示，目前已初步筛选出三种治疗药物，在细胞层面能有效地抑制新冠病毒的复制，在人体上的作用有待临床试验。目前有报道的药物还有阿比朵尔、法匹拉韦、磷酸氯喹等，据统计，目前已有31项临床试验在中国临床试验中心注册。 ")])])]),_c('el-card',{staticClass:"box-card"},[_c('div',{staticClass:"clearfix",attrs:{"slot":"header"},slot:"header"},[_c('span',{staticClass:"title"},[_vm._v("八. 个人如何预防被病毒传染？")])]),_c('div',[_c('p',[_vm._v(" 第一，戴普通医用口罩出门。咳嗽产生的飞沫82%都集中在0.74和-2.12微米，所以绝大多数飞沫核用普通的医用口罩就已经足够了，不需要佩戴N95。 第二，注意打喷嚏的礼仪，在迅雷不及掩耳的0.34秒里，唾液已经传播到70厘米以外了。因此，打喷嚏应使用厚一点的纸巾（或多层重叠），万一没有纸巾或来不及取纸巾，就把头埋在臂弯里，用袖管罩住口鼻来打喷嚏。 第三，勤洗手，并且洗久一点，泡沫搓手至少需要20秒，这大概是相当于把“祝你生日快乐”歌默唱两遍那么长的时间。并尽量避免用手摸自己的脸、揉眼睛。 ")])])]),_c('el-card',{staticClass:"box-card"},[_c('div',{staticClass:"clearfix",attrs:{"slot":"header"},slot:"header"},[_c('span',{staticClass:"title"},[_vm._v("九. 什么时候会有疫苗？")])]),_c('div',[_c('p',[_vm._v(" 美国国家生物技术信息中心(NCBI)发布的文章显示，研究人员已经研究了新冠病毒的基因组，发现了对感染至关重要的蛋白质。来自澳大利亚国立卫生研究院(National Institutes of Health)和至少三家公司的科学家正在研制备选疫苗。 美国国家过敏症和传染病研究所所长安东尼·福奇说：“如果没有遇到任何不可预见的障碍，我们能在未来三个月内进行一期试验……在初步试验之后，可能还需要几个月甚至几年的时间来进行广泛的测试，以证明疫苗安全有效。在最好的情况下，疫苗可能在一年后对公众开放。” ")])])]),_c('el-card',{staticClass:"box-card"},[_c('div',{staticClass:"clearfix",attrs:{"slot":"header"},slot:"header"},[_c('span',{staticClass:"title"},[_vm._v("十. 公共政策如何应对？")])]),_c('div',[_c('p',[_vm._v(" 公共卫生措施（包括社区隔离、及时诊断和严格遵守医疗机构的一般预防措施），这在控制SARS和MERS中都发挥过至关重要的作用。1月30日晚，世界卫生组织宣布新冠疫情为“国际关注的突发公共卫生事件(PHEIC)”，但强调不建议实施旅行和贸易限制。 虽然中国各地都出现了疫情，但1月31日《柳叶刀》发表的香港大学团队最新研究结果表示，相关研究人员相信，仍有可能采取措施来确保控制病毒感染的传播，措施包括对最初的输入病例及早期本地的传播病例进行严格的隔离治疗，受感染的地区应该采取实质性的甚至更加严厉的限制人口流动的措施，例如通过取消大规模集会、学校暂停开学，建立在家安排工作的灵活机制，以此大幅降低人口内部接触率。 这些正是目前中国在坚持做的。中国各地已陆续启动重大突发公共卫生事件I级响应，各省各市各区各村镇陆续出台管控交通的政策，学校推迟了开学时间，企业也推迟了复工的时间。 ")])])]),_c('span',[_vm._v("文章来源：上观新闻 作者：程童 莼洁 曹俊")])],1)],1)],1)}
